
/**
 * 
 * @param {oldtime}   
 * @param {diffTime}   
 * @param {resMode}   arr  max 
 * @back   
 */

const TimeDiffFromNowArr = (oldtime, diffTime, resMode) => {
  const oldTime = new Date(oldtime).valueOf();
  const diffTimes = new Date(diffTime).valueOf();
  if (!oldtime || !oldTime) {
    return null
  }
  const nowTime = diffTimes || Date.now();
  const s_all = Math.round((nowTime - oldTime) / 1000);
  const resStrs = [];
  const res = [];

  const s = s_all % 60;
  let czt = Math.floor(s_all / 60);
  res.unshift(s);
  resStrs.unshift(s + '');
  resStrs

  let m = 0;
  if (czt) {
    m = czt % 60;
    czt = Math.floor(czt / 60);
    res.unshift(m);
    resStrs.unshift(m + '');
  }

  let h = 0;
  if (czt) {
    h = czt % 24;
    czt = Math.floor(czt / 24);
    res.unshift(h);
    resStrs.unshift(h + '');
  }

  let d = 0;
  if (czt) {
    d = czt % 24;
    czt = Math.floor(czt / 24);
    res.unshift(d);
    resStrs.unshift(d + '');
  }
  let mh = 0;
  if (czt) {
    mh = czt % 30;
    czt = Math.floor(czt / 30);
    res.unshift(mh)
    res.unshift(mh + '');
  }
  let y = 0;
  if (czt) {
    y = czt;
    res.unshift(y);
    resStrs.unshift(y + '');
  }
  if (resMode === 'arr') {
    return res
  } else if (resMode === 'max') {
    return resStrs[0]
  }
  return resStrs
}

export {
  TimeDiffFromNowArr
}